@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&family=Ubuntu:wght@500;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}

/* NavBar Styling */

.max-width {
  max-width: 1300px;
  padding: 0 80px;
  margin: auto;
}

.navbar {
  position: fixed;
  width: 100%;
  z-index: 999;
  padding: 30px 0;
  font-family: 'Ubuntu', sans-serif;
  transition: all 0.3s ease;
}

.navbar.active {
  padding: 30px 0;
  background: crimson;
}

.navbar .max-width {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbar .menu li {
  list-style: none;
  display: inline-block;
}

.navbar .menu li a {
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  margin-left: 25px;
  cursor: pointer;
  transition: color 0.3s ease;
}

.navbar .menu li a:hover {
  color: crimson;
}

.navbar .menu-btn i:hover {
  color: crimson;
}

.navbar .icon {
  background-color: grey;
}

.navbar .icon:hover {
  background-color: crimson;
}

.menu-btn .navbar.menu.active li a:hover {
  color: crimson;
}

.navbar.active .icon {
  background: crimson;
}

.navbar.active i {
  color: #111;
}

.navbar.active i:hover {
  background: #111;
}

.navbar.active .menu li a:hover {
  color: #fff;
}

/* Menu button styling */

.menu-btn {
  color: #fff;
  font-size: 23px;
  cursor: pointer;
  display: none;
}

.menu-btn .navbar i:hover {
  color: #111;
}

.menu-btn .navbar.active i {
  color: #fff;
  margin-bottom: 30px;
}

.menu-btn .navbar.active i:hover {
  color: crimson;
}

/* Home section styling */

.home {
  display: flex;
  background: url(/portfolio/static/media/homepage-img.eb002b6a.jpeg) no-repeat center;
  height: 100vh;
  color: #fff;
  min-height: 500px;
  font-family: 'Ubuntu' sans-serif;
}

.home .max-width {
  margin: auto 0 auto 40px;
}

.home .home-content .text-1 {
  font-size: 27px;
}

.home .home-content .text-2 {
  font-size: 75px;
  font-weight: 600;
  margin-left: -3px;
}

.home .home-content .text-3 {
  font-size: 40px;
  margin: 5px 0;
}

.home .home-content .text-3 span {
  color: crimson;
  font-weight: 500;
}

.home .home-content a {
  display: inline-block;
  background: crimson;
  color: #fff;
  font-size: 25px;
  padding: 12px 36px;
  margin-top: 20px;
  border-radius: 6px;
  border: 2px solid crimson;
  transition: all 0.3s ease;
}

.home .home-content a:hover {
  color: crimson;
  background: none;
}

/* All similar content styling code */

section {
  padding: 100px 0;
}

.about, .services, .skills, .contact, .projects, footer {
  font-family: 'Poppins', sans-serif;
}

.about .about-content,
.services .serv-content,
.skills .skills-content,
.contact .contact-content,
.projects .project-content {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

section .title {
  position: relative;
  text-align: center;
  font-size: 40px;
  font-weight: 500;
  margin-bottom: 60px;
  padding-bottom: 20px;
  font-family: 'Ubuntu', sans-serif;
}

section .title::before {
  content: "";
  position: absolute;
  bottom: 0px;
  left: 50%;
  width: 180px;
  height: 3px;
  background: #111;
  transform: translateX(-50%);
}

section .title::after {
  position: absolute;
  bottom: -12px;
  left: 50%;
  font-size: 20px;
  color: crimson;
  padding: 5px;
  background: #fff;
  transform: translateX(-50%);
}

/* Aboutme section styling */

.about .title::after {
  content: "who i am";
}

.about .about-content .left {
  width: 45%;
}

.about .about-content .left img {
  height: 400px;
  width: 400px;
  object-fit: cover;
  border-radius: 6px;
}

.about .about-content .right {
  width: 55%;
}

.about .about-content .right .text {
  font-size: 25px;
  font-weight: 600;
  margin-bottom: 10px;
}

.about .about-content .right .text span {
  color: crimson;
}

.about .about-content .right p {
  text-align: justify;
}

.about .about-content .right a {
  display: inline-block;
  background: crimson;
  color: #fff;
  font-size: 20px;
  font-weight: 500;
  padding: 10px 30px;
  margin-top: 20px;
  border-radius: 6px;
  border: 2px solid crimson;
  transition: all 0.3s ease;
}

.about .about-content .right a:hover {
  color: crimson;
  background: none;
}

/* Services section styling */

.services {
  color: #fff;
  background: #111;
}

.services .title::before {
  background: #fff;
}

.services .title::after {
  background: #111;
  content: "what i provide";
}

.services .serv-content .card,
.skills .skills-content .card,
.projects .project-content .card {
  width: calc(33% - 20px);
  background: #222;
  text-align: center;
  border-radius: 6px;
  margin-bottom: 15px;
  padding: 20px 25px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.services .serv-content .card:hover {
  background: crimson;
}

.services .serv-content .card .box {
  transition: all 0.3s ease;
}

.services .serv-content .card:hover .box {
  transform: scale(1.05);
}

.services .serv-content .card i {
  font-size: 50px;
  color: crimson;
  transition: color 0.3s ease;
}

.services .serv-content .card:hover i {
  color: #fff;
}

.services .serv-content .card .text {
  font-size: 25px;
  font-weight: 500;
  margin: 10px 0 7px 0;
}

/* Skills section styling */

.skills {
  color: #111;
  background: #fff;
}

.skills .title::after {
  content: "always learning";
}

.skills .skills-content .card:hover {
  background: crimson;
}

.skills .skills-content .card:hover .box {
  transform: scale(1.05);
}

.skills .skills-content .card .box {
  transition: all 0.3s ease;
}

.skills .skills-content .card:hover .box {
  transform: scale(1.05);
}

.skills .skills-content .card .text {
  font-size: 25px;
  font-weight: 500;
  margin: 10px 0 7px 0;
  color: #fff;
}

/* Project section styling */

.projects {
  color: #fff;
  background: #111;
}

.projects .title::before {
  background: #fff;
}

.projects .title::after {
  background: #111;
  content: "recent projects";
}

.projects .project-content .card:hover {
  background: crimson;
}

.projects .project-content .card .box {
  transition: all 0.3s ease;
}

.projects .project-content .card:hover .box {
  transform: scale(1.05);
}

.projects .project-content .card .text {
  font-size: 25px;
  font-weight: 500;
  margin: 10px 0 7px 0;
}

.projects h4 {
  text-align: center;
  margin-bottom: 10px;
}

.projects a {
  color: crimson;
  text-decoration: underline;
}

.projects a:hover {
  text-decoration: none;
}

/* Contact section styling */

.contact .title::after {
  content: "get in touch";
}

.contact .contact-content .column {
  width: calc(50% - 30px);
}

.contact .contact-content .text {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 10px;
}

.contact .contact-content .left p {
  text-align: justify;
}

.contact .contact-content .left .icons {
  margin: 10px 0;
}

.contact .contact-content .row {
  display: flex;
  height: 65px;
  align-items: center;
}

.contact .contact-content .row .info {
  margin-left: 30px;
}

.contact .contact-content .row i {
  font-size: 25px;
  color: crimson;
}

.contact .contact-content .info .head {
  font-weight: 500;
}

.contact .contact-content .info .sub-title {
  color: #333;
}

.contact .right form .fields {
  display: flex;
}

.contact .right form .field,
.contact .right form .fields .field {
  height: 45px;
  width: 100%;
  margin-bottom: 15px;
}

.contact .right form .textarea {
  height: 80px;
  width: 100%;
}

.contact .right form .name {
  margin-right: 10px;
}

.contact .right form .email {
  margin-left: 10px;
}

.contact .right form .field input,
.contact .right form .textarea textarea {
  height: 100%;
  width: 100%;
  border: 1px solid lightgrey;
  border-radius: 6px;
  outline: none;
  padding: 0 15px;
  font-size: 17px;
  font-family: 'Poppins', sans-serif;
}

.contact .right form .textarea textarea {
  padding-top: 10px;
  resize: none;
}

.contact .right form .button {
  height: 47px;
  width: 170px;
}

.contact .right form .button input {
  width: 100%;
  height: 100%;
  border: 2px solid crimson;
  background: crimson;
  color: #fff;
  padding: 8px;
  font-size: 20px;
  font-weight: 500;
  border-radius: 6px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.contact .right form .button input:hover {
  color: crimson;
  background: none;
}

/* Footer section styling */

footer {
  background: #111;
  padding: 20px 23px;
  color: #fff;
}

footer span a {
  color: crimson;
  text-decoration: none;
}

footer span a:hover {
  text-decoration: underline;
}

footer .react {
  justify-content: center;
}

footer .icon {
  padding-left: 10px;
  margin-left: 150px;
  background-color: crimson;
}

footer .icon:hover {
  background-color: #fff;
}

/* Responsive media query start */

@media (max-width: 1300px) {
  .home .max-width {
    margin-left: 0px;
  }
  footer .icon {
    margin-left: 120px;
  }
}

@media (max-width: 1150px) {
  .menu-btn {
    display: block;
    z-index: 999;
    color: #fff;
  }
  .menu-btn .navbar.active i {
    color: #fff;
    margin-left: 900px;
  }
  .navbar .menu {
    position: fixed;
    height: 100vh;
    width: 100%;
    left: -100%;
    top: 0;
    background: #111;
    text-align: center;
    padding-top: 80px;
    transition: all 0.3s ease;
  }
  .navbar .menu.active {
    left: 0;
  }
  .navbar .menu li {
    display: block;
  }
  .navbar .menu li a {
    display: inline-block;
    margin: 20px 0;
    font-size: 25px;
  }
  footer .icon {
    margin-left: 110px;
  }
}

@media (max-width: 1104px) {
  .about .about-content .left img {
    height: 350px;
    width: 350px;
  }
  footer .icon {
    margin-left: 80px;
  }
}

@media (max-width: 1050px) {
  footer .icon {
    margin-left: 70px;
  }
  .menu-btn .navbar.active i {
    margin-left: 900px;
  }
}

@media (max-width: 947px) {
  .max-width {
    padding: 0 50px;
  }
}

@media (max-width: 947px) {
  .home .home-content .text-2 {
    font-size: 70px;
  }
  .home .home-content .text-3 {
    font-size: 35px;
  }
  .home .home-content a {
    font-size: 23px;
    padding: 10px 30px;
  }
  .max-width {
    max-width: 800px;
  }
  .about .about-content .column {
    width: 100%;
  }
  .about .about-content .left{
    display: flex;
    justify-content: center;
    margin: 0 auto 60px;
  }
  footer {
    display: flex;
    justify-content: center;
  }
  footer .icon {
    margin-bottom: 15px;
  }
  .about .about-content .right {
    flex: 100% 1;
  }
  .services .serv-content .card,
  .skills .skills-content .card,
  .projects .project-content .card {
    width: calc(50% - 10px);
    margin-bottom: 20px;
  }
  .contact .contact-content .column {
    width: 100%;
    margin-bottom: 35px;
  }
  .menu-btn .navbar.active i {
    margin-left: 600px;
  }
  footer .react {
    margin-left: 150px;
  }
}

@media (max-width: 800px) {
  .menu-btn .navbar.active i {
    margin-left: 500px;
  }
}

@media (max-width: 690px) {
  .max-width {
    padding: 0 23px;
  }
  .home .home-content .text-2 {
    font-size: 60px;
  }
  .home .home-content .text-3 {
    font-size: 32px;
  }
  .home .home-content a {
    font-size: 20px;
  }
  .services .serv-content .card,
  .skills .skills-content .card,
  .projects .project-content .card {
    width: 100%;
  }
  .menu-btn .navbar.active i {
    color: #fff;
    align-items: center;
    margin-left: 370px;
    margin-bottom: 30px;
  }
  footer .react {
    margin-left: 150px;
  }
}

@media (max-width: 500px) {
  .home .home-content .text-2 {
    font-size: 50px;
  }
  .home .home-content .text-3 {
    font-size: 27px;
  }
}
